<template>
  <section>
    <div class="page-header">
      <h1>Projects</h1>
    </div>
    <div class="page-body">
      <base-card v-for="repo in repos" :key="repo.id" style="margin-bottom: 10px;">
        <div class="projects-header">
          <h1>{{ repo.name }}</h1>
        </div>
        <div class="projects-body">
          <p>Lorem ipsum dolor sit amet.</p>
        </div>
      </base-card>
    </div>
  </section>
</template>

<script>
import axios from 'axios';

export default {
  mounted() {
    this.loadData();
  },
  data() {
    return {
      repos: [],
      loadDataError: [],
    }
  },
  methods: {
    async loadData() {
      try {
        const repositories = await axios.get('https://api.github.com/users/rodolforecordon/repos');
        const repos = repositories.data.reduce((previous, current) => {
          const repoDetails = {
            id: current.id,
            name: current.name,
            html_url: current.html_url,
            created_at: current.created_at,
            updated_at: current.updated_at,
            stastargazers_count: current.stargazers_count,
            watchers_count: current.watchers_count,
            language: current.language,
          }
          previous.push(repoDetails);
          return previous;
        }, []);
        console.log(repos);
        this.repos = repos;
      } catch (e) {
        this.loadDataError = 'Error - failed to ge api.github repos.'
        console.log(this.loadDataError)
      }
    }
  }
}
</script>

