<template>
  <ul>
    <a
      v-for="link in navLinks"
      :key="link.name"
      @click="selectComponent(link.name); changeActive(link.name)"
    >
      <li :class="{ selected: link.isActive }">
        <font-awesome-icon class="icons" :icon="link.fontAwesome" />
        {{ link.name }}
      </li>
    </a>
  </ul>
</template>

<script>
import { library } from '@fortawesome/fontawesome-svg-core';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import { faTools } from '@fortawesome/free-solid-svg-icons';
import { faBlog } from '@fortawesome/free-solid-svg-icons';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';

library.add(faUser, faTools, faBlog, faEnvelope);

export default {
  inject: ['selectComponent'],
  data() {
    return {
      navLinks: [
        {
          name: 'About',
          fontAwesome: ['fas', 'user'],
          isActive: true,
        },
        {
          name: 'Projects',
          fontAwesome: ['fas', 'tools'],
          isActive: false,
        },
        {
          name: 'Blog',
          fontAwesome: ['fas', 'blog'],
          isActive: false,
        },
        {
          name: 'Contact',
          fontAwesome: ['fas', 'envelope'],
          isActive: false,
        },
      ],
    }
  },
  methods: {
    changeActive(linkName) {
      this.navLinks.map(el => {
        if (el.name === linkName) el.isActive = true;
        if (el.name !== linkName) el.isActive = false;
      })
    }
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

ul {
  margin: 3vw 0;
}

li {
  list-style-type: none;
  font-family: 'Ubuntu', sans-serif;
  font-size: 1.5vw;
  padding: 1vw 0px;
  width: 80%;
  height: 3vw;
  padding-left: 2vw;
  margin: 0 auto;
  transition: 200ms;
  text-align: left;
  display: flex;
  align-items: center;
}

.selected {
  color: var(--primary-color-darker);
  font-weight: 900;
  font-size: 1.6vw;
}

li:hover {
  color: var(--primary-color-darker);
  font-weight: 900;
  font-size: 1.6vw;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.icons {
  font-size: 70%;
  margin-right: .5vw;
}
</style>
